import React, { Component } from "react";
import { compose } from "redux";
import { connect } from "react-redux";

import actions from "../../../../redux/promotion/customerPriceList/actions";
import Button from "../../../../components/uielements/button";
import CustomerPriceListUpdateForm from "./updateForm";
import IntlMessages from "../../../../components/utility/intlMessages";
import HistoryCustomerPriceList from "./historyCustomerPriceList";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import notification from "../../../../components/notification";
import PageHeader from "../../../../components/utility/pageHeader";
import { Box } from "../../../App/app.style";
import { Spin, Tooltip } from "antd";

const redirecTo = "/dashboard/promotions/customer-price-lists";

class UpdateCustomerPriceList extends Component {
  state = {
    visible: false
  };

  componentDidMount() {
    const {
      editCustomerPriceList,
      getCustomersForCustomerPriceList
    } = this.props;
    const { id } = this.props.match.params;
    editCustomerPriceList(id);
    getCustomersForCustomerPriceList();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.customerPriceList.error) {
      notification("error", this.props.customerPriceList.error);
      this.props.clearError();
    }
    if (this.props.customerPriceList.success) {
      // this.props.history.replace(
      //   redirecTo + "/update/" + this.props.purchaseInvoice.id_purchase_invoice
      // );
      //   this.props.history.replace(redirecTo);
      notification("success", this.props.customerPriceList.success);
      this.props.clearSuccess();
    }
    if (this.props.customerPriceList.warning) {
      // this.props.history.replace(
      //   redirecTo + "/update/" + this.props.purchaseInvoice.id_purchase_invoice
      // );
      //   this.props.history.replace(redirecTo);
      notification("warning", this.props.customerPriceList.warning);
      this.props.clearSuccess();
    }
  }

  handleModalHistory = () => {
    const { visible } = this.state;
    visible
      ? this.setState({ visible: false })
      : this.setState({ visible: true });
  };

  handleSubmit = value => {
    const { id } = this.props.match.params;
    const { updateCustomerPriceList } = this.props;
    updateCustomerPriceList(id, value);
  };

  handleSubmitRejectCustomerPriceList = value => {
    const { rejectCustomerPriceList } = this.props;
    const { id } = this.props.match.params;
    // console.log("value dari reject", value);
    value.status = 3;
    rejectCustomerPriceList(id, value);
  };

  render() {
    const { loading, customerPriceList } = this.props.customerPriceList;
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            marginBottom={true}
            title={<IntlMessages id="customerPriceLists.button.update" />}
          >
            <div style={{ alignItems: "center", display: "flex" }}>
              <Tooltip placement="top" title="Info">
                <Button
                  style={{
                    fontSize: "24px",
                    border: 0
                    // color: theme.palette.warning
                  }}
                  icon="issues-close"
                  onClick={() => this.handleModalHistory()}
                  loading={loading}
                >
                  {/* <IntlMessages id="itemRequests.history" /> */}
                </Button>
              </Tooltip>
              <Button
                style={{ marginLeft: "5px" }}
                onClick={() => this.props.history.replace(redirecTo)}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>

          <HistoryCustomerPriceList
            visible={this.state.visible}
            handleModalHistory={this.handleModalHistory}
            description={
              customerPriceList ? customerPriceList.description : null
            }
          />

          {this.props.customerPriceList.loading ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (
            <CustomerPriceListUpdateForm
              // loading={true}
              auth={this.props.auth}
              confirmCustomerPriceList={this.props.confirmCustomerPriceList}
              customerPriceList={this.props.customerPriceList}
              getCustomersForCustomerPriceList={
                this.props.getCustomersForCustomerPriceList
              }
              getItemsForCustomerPriceList={
                this.props.getItemsForCustomerPriceList
              }
              handleSubmitRejectCustomerPriceList={
                this.handleSubmitRejectCustomerPriceList
              }
              rejectCustomerPriceList={this.props.rejectCustomerPriceList}
              submit={this.handleSubmit}
            />
          )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    customerPriceList: state.customerPriceList,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateCustomerPriceList);
