import React, { Component } from "react";
import moment from "moment";

import Button from "../../../../components/uielements/button";
import EditableTable from "./editableTable";
import IntlMessages from "../../../../components/utility/intlMessages";
import ModalRejectCustomerPriceList from "./modalRejectCustomerPriceList";
import ModalSearchCustomer from "./modalSearchCustomer";
import NumberFormat from "../../../App/NumberFormat";
import notification from "../../../../components/notification";
import { customDot } from "../../../App/app.style";

import {
  // Button as ButtonAntd,
  Col,
  DatePicker,
  Divider,
  Empty,
  Form,
  Input,
  Popconfirm,
  Row,
  Select,
  Spin,
  Steps
} from "antd";
import getLanguage from "../../../App/MyLanguange";

const InputGroup = Input.Group;
const Option = Select.Option;
const Step = Steps.Step;
const { RangePicker } = DatePicker;
const { TextArea } = Input;

class CustomerPriceListUpdateForm extends Component {
  state = {
    customer_name: "",
    dataSource: [],
    expired_date: null,
    id_customer: "",
    id_location: null,
    item: null,
    items: [],
    limit: 0,
    priceListType: null,
    search: null,
    searchCust: null,
    selected_items: [],
    start_date: null,
    visibleModalRejectCustomerPriceList: false,
    visibleModalSearchCustomer: false
  };

  componentDidMount() {}

  componentDidUpdate(prevProps) {
    const { limit } = this.state;
    const { customerPriceList } = this.props.customerPriceList;
    if (limit === 0 && customerPriceList !== null) {
      const { customer_pricelist_details } = customerPriceList;
      customer_pricelist_details.map(item => {
        const { selected_items } = this.state;
        return selected_items.push(item.id_item + "_" + item.id_unit);
      });
      this.setState(
        {
          dataSource: customer_pricelist_details,
          id_location: customerPriceList.id_location,
          limit: 1,
          priceListType: customerPriceList.pricelist_type
        },
        () => {
          if (customerPriceList.customer) {
            this.handleSearchCustomer(
              customerPriceList.customer.customer_name.split(" ")[0]
            );
          }
        }
      );
      this.props.getItemsForCustomerPriceList(customerPriceList.id_location);
    }
  }

  handleChangeRangePicker = (value, dateString) => {
    this.setState({ start_date: dateString[0], expired_date: dateString[1] });
  };

  handleChangeSelectPriceListType = value => {
    const priceListType = value;
    const { getCustomersForCustomerPriceList } = this.props;
    if (this.state.priceListType !== value) {
      if (value === 1) {
        getCustomersForCustomerPriceList();
      }
      this.setState({ priceListType, searchCust: null });
    }
  };

  handleChangeSelectCustomerType = value => {
    this.props.form.setFieldsValue({
      id_customer_type: value
    });
  };

  handleDataSource = () => {
    const customer_pricelist_details = this.props.customerPriceList
      .customerPriceList.customer_pricelist_details;
    const dataSource = [];
    for (var i = 0; i < customer_pricelist_details.length; i++) {
      const newItem = {
        key: customer_pricelist_details[i].id_customer_pricelist_detail,
        index_no: i + 1,
        item_code: customer_pricelist_details[i].item_code,
        id_item: customer_pricelist_details[i].id_item,
        item_name: customer_pricelist_details[i].item_name,
        item_price: customer_pricelist_details[i].item_price,
        discount: customer_pricelist_details[i].discount
      };

      dataSource.push(newItem);
    }
    // return dataSource;
    this.setState(
      { dataSource },
      this.setState({ donePush: true, dataSource: [] })
    );
  };

  handleDeleteItem = key => {
    var dataSource = [...this.state.dataSource];
    // console.log(key, dataSource);
    dataSource = dataSource.filter(item => item.index_no !== key);
    var i = 0;
    let selected_items = [];
    dataSource.map(item => {
      selected_items.push(item.id_item + "_" + item.id_unit);
      i = i + 1;
      item.index_no = i;
    });
    this.setState({
      dataSource,
      selected_items
    });
  };

  handleModalSearchCustomer = () => {
    const { visibleModalSearchCustomer } = this.state;
    visibleModalSearchCustomer
      ? this.setState({ visibleModalSearchCustomer: false })
      : this.setState({ visibleModalSearchCustomer: true });
  };

  handleLoading = () => {
    if (this.props.customerPriceList.loading) return true;
    else return false;
  };

  handleRecord = (action, id) => {
    if (action === "confirm") {
      this.props.confirmCustomerPriceList(id);
    } else {
      this.props.rejectCustomerPriceList(id);
    }
  };

  handleModalRejectCustomerPriceList = () => {
    this.setState({
      visibleModalRejectCustomerPriceList: !this.state
        .visibleModalRejectCustomerPriceList
    });
  };

  handleSearchCustomer = value => {
    const { getCustomersForCustomerPriceList } = this.props;
    this.setState({ searchCust: value });
    getCustomersForCustomerPriceList(value);
  };

  handleSearchItem = value => {
    const { getItemsForCustomerPriceList } = this.props;
    const { id_location } = this.state;
    if (id_location) {
      this.setState({ search: value });
      getItemsForCustomerPriceList(id_location, value);
    }
  };

  handleSelectCustomer = value => {
    const { searchCust } = this.state;
    let setStateData = {};

    if (searchCust) {
      const { getCustomersForCustomerPriceList } = this.props;
      getCustomersForCustomerPriceList();
      setStateData.searchCust = null;
    }
    setStateData.id_customer = value;
    this.setState(setStateData);
  };

  handleSelectItem = value => {
    const { items, id_location } = this.props.customerPriceList;
    const { search, selected_items } = this.state;
    let idItem = value.slice(0, value.indexOf("_"));
    let idUnit = value.slice(value.indexOf("_") + 1, value.length);
    this.setState({
      item: items.filter(
        item => item.id_item === idItem && item.id_unit === idUnit
      )[0]
    });

    if (search && id_location) {
      const { getItemsForCustomerPriceList } = this.props;
      getItemsForCustomerPriceList(id_location);
      this.setState({ search: null });
    }
    // push ke dataSource
    const { dataSource } = this.state;
    const [searchItem] = dataSource.filter(
      item => item.id_item === idItem && item.id_unit === idUnit
    );
    if (searchItem) {
    } else {
      const [newItem] = items.filter(
        item => item.id_item === idItem && item.id_unit === idUnit
      );
      const count = dataSource.length + 1;
      this.setState({
        selected_items: [...selected_items, value],
        dataSource: [
          ...this.state.dataSource,
          {
            ...newItem,
            current_price: newItem.location_price,
            key: newItem.id_item + "_" + newItem.id_unit,
            index_no: count,
            item_price: 0
          }
        ]
      });
    }
  };

  handleSelectLocation = id_location => {
    const { search } = this.state;
    this.setState({
      id_location,
      search: null,
      dataSource: [],
      selected_items: []
    });
    const { getItemsForCustomerPriceList } = this.props;
    if (id_location) {
      getItemsForCustomerPriceList(id_location, search);
    }
  };

  handleSelectModal = value => {
    const { customers } = this.props.customerPriceList;
    const { setFieldsValue } = this.props.form;

    const [find_customer] = customers.filter(
      curr_data => curr_data.customer_code === value
    );
    this.setState({ id_customer: find_customer.id_customer });
    setFieldsValue(
      {
        customer_code: find_customer.customer_code,
        customer_name: find_customer.customer_name
      },
      this.handleModalSearchCustomer()
    );
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, value) => {
      if (!err) {
        value.id_customer_type
          ? (value.id_customer_type = value.id_customer_type)
          : (value.id_customer_type = null);

        value.start_date
          ? (value.start_date = moment(value.start_date, "DD MMMM YYYY").format(
              "YYYY-MM-DD HH:mm:ssss"
            ))
          : (value.start_date = this.props.customerPriceList.customerPriceList.start_date);

        value.expired_date
          ? (value.expired_date = moment(
              value.expired_date,
              "DD MMMM YYYY"
            ).format("YYYY-MM-DD HH:mm:ssss"))
          : (value.expired_date = this.props.customerPriceList.customerPriceList.expired_date);

        const { dataSource } = this.state;

        value.customer_pricelist_details = [];

        dataSource.map(item => {
          value.customer_pricelist_details.push({
            current_price: item.current_price,
            id_unit: item.id_unit,
            id_item: item.id_item,
            item_code: item.item_code,
            item_name: item.item_name,
            discount: item.discount ? item.discount : 0,
            item_price: item.item_price ? item.item_price : 0
          });
        });
        if (value.customer_pricelist_details.length === 0) {
          notification("error", getLanguage({ id: "itemDataEmpty" }));
          this.props.clearError();
        } else {
          this.props.submit(value);
        }
      }
    });
  };

  handleUpdateItem = row => {
    const newData = [...this.state.dataSource];
    const index = newData.findIndex(item => row.index_no === item.index_no);
    const item = newData[index];
    newData.splice(index, 1, {
      ...item,
      ...row
    });
    this.setState({ dataSource: newData });
  };

  render() {
    // get data from state
    // console.log(data);

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    //let permissions = this.props.permissions;

    // console.log(permissions);

    const status = () => {
      let curr = 0;
      switch (data.status) {
        case 1:
          curr = 0;
          break;
        case 2:
          curr = 1;
          break;
        case 3:
          curr = 2;
          break;
        default:
          curr = 0;
          break;
      }
      return curr;
    };

    // inline style
    var blockDisplay = {
      display: "block"
    };

    // form layout
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    const { search } = this.state;
    const { getFieldDecorator } = this.props.form;
    const {
      customerPriceList,
      customers,
      customer_types,
      items,
      id_location,
      loadingCustomers,
      loadingItems,
      loadingSubmit,
      loading,
      locations
    } = this.props.customerPriceList;

    const data = customerPriceList ? customerPriceList : [];

    // console.log("customer types ", customer_types);

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        {/* {this.state.donePush === true ? this.handleDataSource() : null} */}
        <ModalRejectCustomerPriceList
          visible={this.state.visibleModalRejectCustomerPriceList}
          handleModal={this.handleModalRejectCustomerPriceList}
          submit={this.props.handleSubmitRejectCustomerPriceList}
          loading={loading}
        />
        <ModalSearchCustomer
          customersData={customers}
          handleModal={this.handleModalSearchCustomer}
          handleSelectModal={this.handleSelectModal}
          visible={this.state.visibleModalSearchCustomer}
        />
        <Row style={{ margin: "12px 0px" }}>
          <Col span={16}>
            <Steps size="small" progressDot={customDot} current={status()}>
              <Step title={getLanguage({ id: "new" })} />
              <Step title={getLanguage({ id: "confirmed" })} />
              <Step title={getLanguage({ id: "rejected" })} />
            </Steps>
          </Col>
        </Row>
        {/* Form untuk customer price list data */}
        <Divider orientation="left" style={{ fontSize: 14 }}>
          <span>
            <IntlMessages id="detail" />
          </span>
        </Divider>
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div
            className="isoColumn-2 isoFormGroupComp"
            style={{ padding: "0 15px 0 0" }}
          >
            {/* Form item untuk pricelist code */}
            <Form.Item
              label={<IntlMessages id="customerPriceLists.priceListCode" />}
            >
              {getFieldDecorator("pricelist_code", {
                initialValue: data.pricelist_code,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 30,
                    message: <IntlMessages id="maxLength30" />
                  }
                ]
              })(<Input disabled={data.status !== 1 ? true : false} />)}
            </Form.Item>

            {/* Form item untuk Location */}
            <Form.Item label={<IntlMessages id="sidebar.location" />}>
              {getFieldDecorator("id_location", {
                initialValue: data.id_location,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  disabled={data.status !== 1 ? true : false}
                  filterOption={true}
                  onChange={this.handleSelectLocation}
                  optionFilterProp="children"
                  placeholder={
                    <IntlMessages id="purchaseReceipts.filter.select.location" />
                  }
                  showSearch
                >
                  {locations ? (
                    locations.map(data => (
                      <Option key={data.id_location} value={data.id_location}>
                        {data.location_code} ({data.location_name})
                      </Option>
                    ))
                  ) : (
                    <Option value="" />
                  )}
                </Select>
              )}
            </Form.Item>

            <Form.Item label={<IntlMessages id="priceListRangeDate" />}>
              {getFieldDecorator("range_date", {
                initialValue: [
                  moment(data.start_date, "YYYY-MM-DD"),
                  moment(data.expired_date, "YYYY-MM-DD")
                ],
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <RangePicker
                  allowClear
                  disabled={data.status !== 1 ? true : false}
                  onChange={this.handleChangeRangePicker}
                  format="DD MMMM YYYY"
                  style={{
                    width: "100%"
                  }}
                />
              )}
            </Form.Item>
          </div>

          <div
            className="isoColumn-2 isoFormGroupComp"
            style={{ padding: "0 0 0 15px" }}
          >
            {/* Form item untuk pricelist type */}
            <Form.Item
              label={<IntlMessages id="customerPriceLists.priceListType" />}
            >
              {getFieldDecorator("pricelist_type", {
                initialValue: this.state.priceListType,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  disabled={data.status !== 1 ? true : false}
                  onChange={this.handleChangeSelectPriceListType}
                  placeholder={
                    <IntlMessages id="customerPriceLists.priceListType.select" />
                  }
                >
                  <Option value={1}>
                    {<IntlMessages id="customerCode" />}
                  </Option>
                  <Option value={2}>
                    {<IntlMessages id="sidebar.customerType" />}
                  </Option>
                </Select>
              )}
            </Form.Item>

            {this.state.priceListType === parseInt(2) ? (
              // {/* Form item untuk Customer Type - Price List Type 2 */}
              <Form.Item label={<IntlMessages id="sidebar.customerType" />}>
                {// const
                getFieldDecorator("id_customer_type", {
                  initialValue: data.id_customer_type,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <Select
                    disabled={data.status !== 1 ? true : false}
                    onChange={this.handleChangeSelectCustomerType}
                    placeholder={
                      <IntlMessages id="customers.filter.select.customerType" />
                    }
                  >
                    {customer_types ? (
                      customer_types.map(item => (
                        <Option
                          key={item.id_customer_type}
                          value={item.id_customer_type}
                        >
                          {item.customer_type_code} ({item.customer_type_name})
                        </Option>
                      ))
                    ) : (
                      <Option value="" />
                    )}
                  </Select>
                )}
              </Form.Item>
            ) : (
              // Price list type 1
              <InputGroup className="isoDetailDataFlex">
                {/* <Col span={12}>
                  <Form.Item
                    label={
                      <IntlMessages id="customerPriceLists.customerCode" />
                    }
                  >
                    {getFieldDecorator("customer_code", {
                      initialValue: customer_code,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Input
                        disabled={data.status !== 1 ? true : false}
                        readOnly={true}
                        onClick={this.handleModalSearchCustomer}
                        addonAfter={
                          <ButtonAntd
                            style={{
                              width: "100%",
                              height: "30px",
                              border: "0px",
                              backgroundColor: "transparent"
                            }}
                            loading={this.handleLoading()}
                            onClick={this.handleModalSearchCustomer}
                            icon="search"
                          />
                        }
                      />
                    )}
                  </Form.Item>
                </Col> */}
                <Col span={24}>
                  {/* Form item untuk customer name */}
                  <Form.Item label={<IntlMessages id="sidebar.customer" />}>
                    {getFieldDecorator("id_customer", {
                      initialValue: data.id_customer,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Select
                        allowClear
                        disabled={data.status !== 1 ? true : false}
                        filterOption={false}
                        loading={loadingCustomers}
                        notFoundContent={
                          loadingCustomers ? (
                            <Spin size="small" />
                          ) : (
                            <Empty description={<IntlMessages id="noData" />} />
                          )
                        }
                        onSelect={this.handleSelectCustomer}
                        onSearch={this.handleSearchCustomer}
                        placeholder={<IntlMessages id="select.customer" />}
                        showSearch
                        style={{ width: "100%" }}
                      >
                        {customers && customers.length > 0
                          ? customers.map(data => (
                              <Option
                                key={data.id_customer}
                                value={data.id_customer}
                              >
                                {data.customer_code} ({data.customer_name})
                              </Option>
                            ))
                          : null}
                      </Select>
                    )}
                  </Form.Item>
                </Col>
              </InputGroup>
            )}

            {/* Form item untuk description */}
            <Form.Item label={<IntlMessages id="suppliers.add.notes" />}>
              {getFieldDecorator("note", {
                initialValue: data.note,
                rules: []
              })(
                <TextArea
                  disabled={data.status !== 1 ? true : false}
                  rows={4}
                />
              )}
            </Form.Item>
          </div>
        </div>

        {/* Form untuk Item Data */}
        <div style={blockDisplay}>
          <Divider orientation="left" style={{ fontSize: 14 }}>
            <IntlMessages id="itemDetails" />
          </Divider>
          <div className="isoDetailDataFlex" style={{ padding: 0 }}>
            <div
              className="isoColumn-1 isoFormGroupComp"
              style={{ padding: 0 }}
            >
              <Row style={{ marginBottom: "40px" }}>
                <Col span={24}>
                  <Form.Item {...formItemLayout}>
                    <Select
                      allowClear
                      disabled={data.status !== 1 ? true : false}
                      filterOption={false}
                      showSearch
                      value={search ? search : []}
                      // optionFilterProp="children"
                      placeholder={
                        <IntlMessages id="purchaseOrders.item.placeholder" />
                      }
                      loading={loadingItems}
                      notFoundContent={
                        loadingItems ? (
                          <Spin size="small" />
                        ) : id_location ? (
                          <Empty description={<IntlMessages id="noData" />} />
                        ) : (
                          <Empty
                            description={
                              <IntlMessages id="purchaseOrders.item.noLocation" />
                            }
                          />
                        )
                      }
                      onSelect={this.handleSelectItem}
                      onSearch={this.handleSearchItem}
                    >
                      {items.map(data =>
                        !this.state.selected_items.includes(
                          data.id_item + "_" + data.id_unit
                        ) ? (
                          <Option
                            key={data.id_item + "_" + data.id_unit}
                            value={data.id_item + "_" + data.id_unit}
                            style={{ width: "100%" }}
                          >
                            <Row>
                              <Col span={14}>
                                <span>
                                  {data.item_code} ({data.item_name})
                                </span>
                              </Col>
                              <Col span={10}>
                                <span>
                                  {data.unit_name} (
                                  <NumberFormat
                                    value={parseFloat(data.unit_conversion)}
                                  />{" "}
                                  {" " + data.base_unit.unit_name})
                                </span>
                              </Col>
                            </Row>
                          </Option>
                        ) : null
                      )}
                    </Select>
                  </Form.Item>
                </Col>
                <Col span={24}>
                  <EditableTable
                    dataSource={this.state.dataSource}
                    status={data.status}
                    handleDeleteItem={this.handleDeleteItem}
                    handleUpdateItem={this.handleUpdateItem}
                  />
                </Col>
              </Row>
            </div>
          </div>
        </div>

        {/* Submit Button */}
        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginRight: "5px" }}
            loading={loadingSubmit}
            disabled={
              data.status === 2 ||
              data.status === 3 ||
              !permissions.includes("Update Customer Pricelist")
                ? true
                : false
            }
          >
            <IntlMessages id="button.submit" />
          </Button>

          {/* confirm */}
          <Popconfirm
            title={<IntlMessages id="customerPriceLists.confirm" />}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
            onConfirm={this.handleRecord.bind(
              this,
              "confirm",
              data.id_customer_pricelist
            )}
          >
            <Button
              style={{
                marginRight: "5px",
                marginLeft: "5px",
                padding: "0px 15px"
              }}
              disabled={
                data.status === 2 ||
                data.status === 3 ||
                !permissions.includes("Confirm Customer Pricelist")
                  ? true
                  : false
              }
              // loading={loading || loadingSubmit}
            >
              <IntlMessages id="customerPriceLists.button.confirm" />
            </Button>
          </Popconfirm>

          {/* cancel */}
          {/* <Popconfirm
            title={<IntlMessages id="customerPriceLists.button.reject" />}
            onConfirm={this.handleRecord.bind(
              this,
              "reject",
              this.state.data.id_customer_pricelist
            )}
          >
            
          </Popconfirm> */}

          <Button
            style={{ marginLeft: "5px", padding: "0px 15px" }}
            disabled={
              data.status === 1 ||
              data.status === 3 ||
              !permissions.includes("Cancel Customer Pricelist")
                ? true
                : false
            }
            onClick={this.handleModalRejectCustomerPriceList}
            // loading={loading || loadingSubmit}
          >
            <IntlMessages id="customerPriceLists.button.reject" />
          </Button>
        </Form.Item>

        {/* Form */}
      </Form>
    );
  }
}

const WrappedCustomerPriceListUpdateForm = Form.create({
  name: "update_customer_price_list"
})(CustomerPriceListUpdateForm);

export default WrappedCustomerPriceListUpdateForm;
