import React, { Component } from "react";
import IntlMessages from "../../../../components/utility/intlMessages";
import { Form, Input, Col, Row, Modal } from "antd";
import getLanguage from "../../../App/MyLanguange"

class ModalRejectCustomerPriceList extends Component {
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator } = this.props.form;
    const { loading } = this.props;

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    return (
      <Modal
        title={<IntlMessages id="customerPriceLists.button.reject" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="35%"
        okText={getLanguage({ id: "button.ok" })}
        cancelText={getLanguage({ id: "button.cancel" })}
        okButtonProps={{ loading: loading }}
        cancelButtonProps={{ loading: loading }}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <Row type="flex" justify="start" gutter={6}>
            <Col span={24}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="cancelledReason" />}
              >
                {getFieldDecorator("rejected_reason", {
                  //initialValue: data.unordered_reason,
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(<Input onPressEnter={this.handleSubmit} />)}
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </Modal>
    );
  }
}

const WrappedDetailModal = Form.create({ name: "unordered_reason_modal" })(
  ModalRejectCustomerPriceList
);

export default WrappedDetailModal;
